:root {
  --color-primary: #2584ff;
  --color-secondary: #00d9ff;
  --color-accent: #ff3400;
  /* --color-accent: #116530; */
  --color-headings: #1b0760;
  --color-body: #918ca4;
  --color-body-darker: #5c5577;
  --color-border: #ccc;
  --border-radius: 30px;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

/* Typography */

::selection {
  background: #2584ff;
  background: var(--color-primary);
  color: #fff;
}

html {
  font-size: 62.5%;
}

img {
  width: 100%;
}

body {
  font-family: Inter, Arial, Helvetica, sans-serif;
  font-size: 2rem;
  line-height: 1.5;
  color: #918ca4;
  color: var(--color-body);
}

h1,
h2,
h3 {
  color: #1b0760;
  color: var(--color-headings);
  margin-bottom: 1rem;
  line-height: 1.1;
  font-weight: 600;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 4rem;
}

h3 {
  font-size: 2.8rem;
  font-weight: 600;
  line-height: 1.3;
}

p {
  margin-top: 0;
}

@media screen and (min-width: 1024px) {
  body {
    font-size: 1.8rem;
  }
  h1 {
    font-size: 8rem;
  }
  h2 {
    font-size: 4rem;
  }
  h3 {
    font-size: 2.4rem;
  }
}

/* Links */

a {
  text-decoration: none;
}

/* home page */
.home-page {
  padding-top: 100px;
}
/* Badges */

.badge {
  border-radius: 20px;
  font-size: 2rem;
  font-weight: 600;
  padding: 0.5rem 2rem;
  white-space: nowrap;
}

.badge--primary {
  background: #2584ff;
  background: var(--color-primary);
  color: #fff;
}

.badge--secondary {
  background: #00d9ff;
  background: var(--color-secondary);
  color: #fff;
}

.badge--small {
  font-size: 1.6rem;
  padding: 0.5rem 1.5rem;
}

.badge-category {
  cursor: pointer;
}
.badge-category:hover {
  opacity: 0.95;
}
@media screen and (min-width: 1024px) {
  .badge {
    font-size: 1.7rem;
  }
  .badge--small {
    font-size: 1.2rem;
  }
}

/* Lists */

.list {
  list-style: none;
  padding-left: 0;
}

/* Icons */

.icon {
  width: 40px;
  height: 40px;
}

.icon--small {
  width: 20px;
  height: 20px;
}

.icon--medium {
  width: 30px;
  height: 30px;
}

.icon--primary {
  fill: #2584ff;
  fill: var(--color-primary);
}

.icon--white {
  fill: #fff;
}

.icon-container {
  background: #f3f9fa;
  width: 64px;
  height: 64px;
  border-radius: 100%;
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.icon-container--accent {
  background: #ff3400;
  background: var(--color-accent);
}

/* Buttons */

.btn {
  border-radius: 40px;
  border: 0;
  cursor: pointer;
  font-size: 1.8rem;
  font-weight: 600;
  margin: 1rem 0;
  outline: 0;
  padding: 2rem 4vw;
  text-align: center;
  text-transform: uppercase;
  white-space: nowrap;
}

.btn .icon {
  width: 2rem;
  height: 2rem;
  margin-right: 1rem;
  vertical-align: middle;
}

.btn--primary {
  background: #2584ff;
  background: var(--color-primary);
  color: #fff;
}

.btn--primary:hover {
  background: #3a8ffd;
  color: #fff;
}

.btn--secondary {
  background: #00d9ff;
  background: var(--color-secondary);
  color: #fff;
}

.btn--secondary:hover {
  background: #05cdf0;
  color: #fff;
}

.btn--accent {
  background: #ff3400;
  background: var(--color-accent);
  color: #fff;
}

.btn--accent:hover {
  background: #ec3000;
  color: #fff;
}

.btn--outline {
  background: #fff;
  color: #1b0760;
  color: var(--color-headings);
  border: 2px solid #1b0760;
  border: 2px solid var(--color-headings);
}

.btn--outline:hover {
  background: #1b0760;
  background: var(--color-headings);
  color: #fff;
}

.btn--stretched {
  padding-left: 6rem;
  padding-right: 6rem;
}

.btn--block {
  width: 100%;
  display: inline-block;
}

.btn--square {
  border-radius: 5px;
  padding: 1rem 4vw;
}
@media screen and (min-width: 1024px) {
  .btn {
    font-size: 1.5rem;
  }
}

/* Inputs */

.search-input {
  border-radius: 30px;
  border-radius: var(--border-radius);
  border: 1px solid #ccc;
  border: 1px solid var(--color-border);
  color: #1b0760;
  color: var(--color-headings);
  font-size: 2rem;
  outline: 0;
  padding: 1.5rem 3.5rem;
}

::-webkit-input-placeholder {
  color: #cdcbd7;
}

:-ms-input-placeholder {
  color: #cdcbd7;
}

::placeholder {
  color: #cdcbd7;
}

.search-input-group {
  border: 1px solid #ccc;
  border: 1px solid var(--color-border);
  border-radius: 30px;
  border-radius: var(--border-radius);
  display: -webkit-flex;
  display: flex;
}

.search-input-group .search-input {
  border: 0;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  padding: 1.5rem 2rem;
  width: 0;
}

.search-input-group .btn {
  margin: 4px;
}

@media screen and (min-width: 1024px) {
  .search-input {
    font-size: 1.5rem;
  }
}

/* Cards */

.card {
  border-radius: 7px;
  box-shadow: 0 0 20px 10px #f3f3f3;
  overflow: hidden;
}

.card__header,
.card__body {
  padding: 2rem 7%;
}

/* Media */

.media {
  display: -webkit-flex;
  display: flex;
  margin-bottom: 4rem;
}

.media__title {
  margin-top: 0;
}

.media__body {
  margin: 0 2rem;
}

.media__image {
  margin-top: 1rem;
}

/* Quotes */

.quote {
  font-size: 3rem;
  font-style: italic;
  color: #5c5577;
  color: var(--color-body-darker);
  line-height: 1.3;
}

.quote__text::before {
  content: open-quote;
}

.quote__text::after {
  content: close-quote;
}

.quote__author {
  font-size: 2rem;
  font-weight: 500;
  font-style: normal;
  margin-bottom: 0;
}

.quote__organization {
  color: #1b0760;
  color: var(--color-headings);
  opacity: 0.4;
  font-size: 1.5rem;
  font-style: normal;
}

.quote__line {
  position: relative;
  bottom: 10px;
}

@media screen and (min-width: 1024px) {
  .quote {
    font-size: 2rem;
  }
  .quote__author {
    font-size: 2.4rem;
  }
  .quote__organization {
    font-size: 1.6rem;
  }
}

/* Grids */

.grid {
  display: grid;
}

@media screen and (min-width: 768px) {
  .grid--1x2 {
    grid-template-columns: repeat(2, 1fr);
  }
  .listings--grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media screen and (min-width: 1024px) {
  .grid--1x3 {
    grid-template-columns: repeat(3, 1fr);
  }

  .listings--grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

/*-------------listings grid----------------*/

.listings--grid {
  margin: 1rem auto;
  grid-gap: 1rem;
}
/* Testimonials */

.testimonial {
  padding: 3rem;
}

.testimonial .quote {
  margin: 2rem 0;
  font-size: 110%;
}

.testimonial__image {
  position: relative;
  margin: 0 3rem;
}

.testimonial__image > .icon-container {
  position: absolute;
  top: 3rem;
  right: -32px;
}

@media screen and (min-width: 768px) {
  .testimonial__image {
    margin: 0;
  }
  .testimonial .quote {
    margin: 5rem 0 0 6rem;
    font-size: 120%;
    line-height: 1.5;
  }
}

/* Callouts */

.callout {
  padding: 4rem;
  border-radius: 5px;
}

.callout--primary {
  background: #2584ff;
  background: var(--color-primary);
  color: #fff;
}

.callout__heading {
  color: #fff;
  margin-top: 0;
  font-size: 3rem;
}

.callout .btn {
  justify-self: center;
  -webkit-align-self: center;
          align-self: center;
}

.callout__content {
  text-align: center;
}

@media screen and (min-width: 768px) {
  .callout .grid--1x2 {
    grid-template-columns: 1fr auto;
  }
  .callout__content {
    text-align: left;
  }
  .callout .btn {
    justify-self: start;
    margin: 0 2rem;
  }
}

/* Collapsibles */

.collapsible__header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.collapsible__heading {
  margin-top: 0;
  font-size: 3rem;
}

.collapsible__chevron {
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}

.collapsible__content {
  max-height: 0;
  overflow: hidden;
  opacity: 0;
  transition: all 0.3s;
}

.collapsible--expanded .collapsible__chevron {
  -webkit-transform: rotate(0);
          transform: rotate(0);
}

.collapsible--expanded .collapsible__content {
  max-height: 100%;
  opacity: 1;
}

/* Blocks */

.block {
  padding: 2rem;
  padding-bottom: 0.5rem;
}

.block__header {
  text-align: center;
  margin-bottom: 4rem;
}

.block__heading {
  margin-top: 0;
}

.block--dark {
  background: #000;
  color: #7b858b;
}

.block--dark h1,
.block--dark h2,
.block--dark h3 {
  color: #fff;
}

.container {
  max-width: 1140px;
  margin: 0 auto;
}

/* Hero */

.hero__tagline {
  font-size: 2rem;
  /* color: #b9c3cf; */
  color: #918ca4;
  color: var(--color-body);
  letter-spacing: 1px;
  margin: 2rem 0;
}

@media screen and (min-width: 768px) {
  .hero__content {
    text-align: left;
    -webkit-align-self: center;
            align-self: center;
  }
}

@media screen and (min-width: 1024px) {
  .hero {
    padding-top: 0;
  }
}

/* Domain Block */

.block-domain .search-input-group {
  box-shadow: 0 0 30px 20px #e6ebee;
  border: 0;
  margin: 2rem auto;
  max-width: 670px;
}

.block-domain__prices {
  color: #1b0760;
  color: var(--color-headings);
  display: grid;
  grid-template-columns: repeat(2, 1fr);

  /* grid-template-rows: repeat(2, 6rem); */
  /* 5 is for 5 rows see how many categories u  have depending on that give that perticular number */
  /* grid-template-rows: repeat(5, 6rem); */
  grid-template-rows: repeat(3, 6rem);

  font-size: 2rem;
  font-weight: 600;
  justify-items: center;
  max-width: 700px;
  margin: 0 auto;
}

@media screen and (min-width: 768px) {
  .block-domain__prices {
    grid-template-columns: repeat(auto-fit, minmax(10rem, 1fr));
    grid-template-rows: repeat(1, 6rem);
  }
  /* 
  .block-domain__prices li:nth-child(8) {
    grid-column: 1/3;
  } */
}

/* Features */

.feature {
  grid-gap: 4rem 2rem;
  gap: 4rem 2rem;
  margin: 12rem 0;
}

.feature:first-of-type {
  margin-top: 6rem;
}

.feature__heading {
  margin: 1rem 0;
}

@media screen and (min-width: 768px) {
  .feature:nth-of-type(even) .feature__content {
    -webkit-order: 2;
            order: 2;
  }
}

/* Footer */

.footer {
  background: #232323;
  padding-top: 10rem;
}

.footer a {
  color: #777;
  transition: color 0.3s;
}

.footer a:hover {
  color: #fff;
}

.footer__section {
  padding: 2rem;
  border-bottom: 1px solid #393939;
}

.footer__section .list {
  margin: 0;
}

.footer__heading {
  text-transform: uppercase;
  font-weight: 600;
  font-size: 2.5rem;
  line-height: 1.5;
}

.footer__brand {
  margin-top: 5rem;
  text-align: center;
}

.footer__brand img {
  /* max-width: 230px; */
  max-width: 200px;
  margin-bottom: 0.5rem;
}

.footer__copyright {
  font-size: 2.1rem;
  color: #fff;
  opacity: 0.3;
}

@media screen and (min-width: 768px) {
  .footer {
    font-size: 1.5rem;
  }
  .footer__sections {
    grid-template-columns: repeat(auto-fit, minmax(10rem, 1fr));
  }
  .footer .collapsible__chevron {
    display: none;
  }
  .footer .collapsible__content {
    opacity: 1;
    max-height: 100%;
  }
  .footer__brand {
    -webkit-order: -1;
            order: -1;
    margin-top: 1rem;
  }
  .footer__copyright {
    font-size: 1.5rem;
  }
  .footer__section {
    border: 0;
  }
  .footer__heading {
    font-size: 1.6rem;
  }
}

/* Sign-up Callout */

.callout-signup {
  -webkit-transform: translateY(5rem);
          transform: translateY(5rem);
}

/* playstore */
.playstore {
  width: 60%;
  border-radius: 1rem;
}

/* listing details page */

/* carousel */
.listing-carousel img {
  display: block;
  height: 400px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.carousel-control-next .sr-only,
.carousel-control-prev .sr-only {
  opacity: 0;
}

.carousel-control-next,
.carousel-control-prev {
  width: 18%;
}

.carousel-control-prev {
  width: 3rem;
  margin-left: 3rem;
}

.details-box {
  padding: 1rem;
}

@media (max-width: 700px) {
  .details-box,
  .details-box-right {
    margin-top: 15px;
    border-top: 2px #132c20 solid;
    border-bottom: 2px #132c20 solid;
  }

  .details-box-right {
    margin-bottom: 15px;
  }
}

.details-box,
.listing-carousel,.details-box-right  {
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

/* listing card */
.picture-card,
.listing-card {
  position: relative;
}

.listing-card__image {
  height: 210px;
  object-fit: cover;
}

.listing-card__text {
  display: "flex";
  -webkit-justify-content: "space-between";
          justify-content: "space-between";
  padding: 1rem 0.5rem 0 0.5rem;
}
.listing-card__text h4 {
  font-weight: 530;
}

.listing-card-showcase {
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 0.5rem;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  transition: box-shadow 0.3s;
}

.listing-card-showcase:hover {
  box-shadow: 0 0 2px 1px rgba(2, 2, 8, 0.5);
}

.listing-card-overlay,
.picture-overlay {
  height: 50px;
  left: 1rem;
  opacity: 0;
  position: absolute;
  top: 2rem;
  transition: 0.2s ease;
  width: 50px;
}

.overlay__icon {
  background-color: #fff;
  cursor: pointer;
  padding: 7px;
  border: none;
  margin-top: 1rem;
}

.overlay__icon .edit--icon {
  padding-bottom: 2px;
}

.listing-card:hover .listing-card-overlay,
